import React from 'react';
import { connect } from 'react-redux';
import {
  CustomSelect,
  CustomDatePicker,
} from '../components/Filters';
import {
  SELECT_TYPE_LABEL,
} from '../constants/Filters/SelectOptionValue';
import PricingCalendarButton from '../components/LeaseTerm/PricingCalendarButton';
import '../styles/App.css';
import { setLeaseTerm } from '../redux/slices/userSelectionSlice';
import { setTerm, setMoveInDate } from '../redux/slices/searchFilterSlice';
import PricingCalendarModal from '../components/Modals/PricingCalendarModal';
import { format, isAfter, parse, parseISO, startOfDay } from 'date-fns';
import { loadBestUnitPricing, loadUnitPricing, loadRentableItems } from '../services/UnitQuery/unitQueries';
import Spinner from '../components/Common/Spinner';
import BackButton from '../components/Common/BackButton';
import { Screens, SELECTION_HEADER } from '../constants/Screens/Screens';
import { setScreen } from '../redux/slices/configurationSlice';
import { setLeaseTermOptions } from '../redux/slices/searchResultSlice';
import { getLeaseTermOptions, isBetweenPricingDates } from '../services/utils/pricingHelper';
import SelectionHeader from '../components/Common/SelectionHeader';
import ConfirmSelectionModal from '../components/Modals/ConfirmSelectionModal';
import UserRegistrationModal from '../components/Modals/UserRegistrationModal';
import PricingResults from '../components/LeaseTerm/PricingResults';
import store from '../redux/store';
import { ErrorHandler } from '../services/utils/errorHandlingUtil';
import PropTypes from 'prop-types';

class Pricing extends React.Component {

  dispatch = this.props.dispatch;
  apartment = store.getState().userSelection.apartment;
  filters = store.getState().searchFilter;
  selection = store.getState().userSelection;
  mininmumDate = this.filters.leaseTermMinDate;

  constructor(props) {
    super(props);
    this.state = {
      showUserRegistrationModal: false,
      showConfirmSelectionModal: false,
      showPricingCalendarModal: false,
      hasRentableItem: false,
      pricingMoveInDate: this.filters.moveInDate || format(new Date(), 'yyyy-MM-dd'),
      pricingTerm: this.filters.term || 12,
      pricingResults:[],
      loading: true,
      initialLoading: true,
      selectedTerm: this.filters.term || 12,
      bestValueTerm: {},
      greatValueTerm: {},
      pricingTermOptions: [],
      bestValueLoaded: false,
    };
  }

  closeConfirmSelectionModal = () => {
    this.dispatch(setLeaseTerm({}));
    this.setState({ showConfirmSelectionModal: false });
  };

  closeUserRegistrationModal = () => {
    this.setState({ showUserRegistrationModal: false });
  };

  openRegistrationModal = () => {
    this.setState({ showConfirmSelectionModal: false });
    this.setState({ showUserRegistrationModal: true });
  };

  openConfirmSelectionModal = () => {
    this.setState({ showUserRegistrationModal: false });
    this.setState({ showConfirmSelectionModal: true });
  };

  getRentableItems = async () => {
    try {
      const data = await loadRentableItems(this.apartment.id);
      this.setState({ hasRentableItem: data.length !== 0 });
    } catch (error) {
      this.props.handleError(error);
    }
  };


  handleSelectLeaseTerm = async (data, moveInDate) => {
    this.dispatch(setLeaseTerm(data));
    this.dispatch(setTerm(data.term));
    this.dispatch(setMoveInDate(format(parse(moveInDate, 'MM/dd/yyyy', new Date()), 'yyyy-MM-dd')));

    if (this.state.hasRentableItem) {
      this.dispatch(setScreen(Screens.ITEMS));
    }
    this.openConfirmSelectionModal();
  };

  viewPricingCalendar = () => {
    this.setState({ showPricingCalendarModal: !this.state.showPricingCalendarModal });
  };

  closePricingCalendar = () => {
    this.setState({ showPricingCalendarModal: !this.state.showPricingCalendarModal });
  };

  savePricingCalendar = (selectedTerm, selectedDate) => {
    this.setState({
      pricingTerm: selectedTerm,
      pricingMoveInDate: selectedDate,
    });
    this.closePricingCalendar();
  };

  getBestGreatPricing = async () => {
    try {
      const data = await loadBestUnitPricing(this.apartment.id);

      const date = startOfDay(parseISO(this.mininmumDate));
      if (data && data.length > 0) {
        const best = data[0];
        best.displayDate = best.startDate;
        if (isAfter(parseISO(best.endDate), date)) {
          if (isBetweenPricingDates(date, best)) {
            best.displayDate = format(date, 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000');
          }
          this.setState({ bestValueTerm: best });
        }

        const great = data[1];
        great.displayDate = great.startDate;
        if (isAfter(parseISO(great.endDate), date)) {
          if (isBetweenPricingDates(date, great)) {
            great.displayDate = format(date, 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000');
          }
          this.setState({ greatValueTerm: great });
        }
      }

    } catch (error) {
      this.props.handleError(error);
    } finally {
      this.setState({ bestValueLoaded: true });
    }
  };

  getUnitPricing = async () => {
    const moveDate = format(parseISO(this.state.pricingMoveInDate), 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000'); // +0000 instead of XX getting actual timezone
    const pricingTerm = this.state.pricingTerm;
    const body = {
      'moveInDate': moveDate,
    };
    if (!this.state.initialLoading) {
      body.term = pricingTerm;
    }
    try {
      const { records } = await loadUnitPricing(this.apartment.id, body);

      if (records && records.length > 0) {
        if (this.state.initialLoading) {
          this.getBestGreatPricing();

          const filtered = [];
          const options = getLeaseTermOptions(records, pricingTerm);
          let filterTerm = pricingTerm;

          this.setState({ pricingTermOptions: options });
          if (!(pricingTerm in options)) {
            filterTerm = options[Object.keys(options)[0]].value;
            this.setState({ selectedTerm: filterTerm });
          }
          records.forEach((priceRecord) => {
            if (priceRecord.term === filterTerm) {
              filtered.push(priceRecord);
            }
          });
          this.setState({
            pricingResults: filtered,
            initialLoading: false,
          });
        } else {
          this.setState({
            selectedTerm: pricingTerm,
            pricingResults: records,
          });
        }
      } else {
        this.setState({ bestValueLoaded: true });
      }

    } catch (error) {
      this.props.handleError(error);
    } finally {
      this.setState({ loading: false });
    }
  };

  componentDidMount() {
    if (Object.keys(this.selection.floorPlan).length === 0) {
      this.dispatch(setScreen(Screens.FLOORPLANS));
    }
    this.setState({ loading: true });
    this.getRentableItems();
    const delayDebounceFn = setTimeout(() => {
      this.getUnitPricing();
      this.dispatch(setLeaseTermOptions(this.state.pricingTermOptions));
    }, 1000);
    return () => clearTimeout(delayDebounceFn);
  }

  setPricingMoveInDate = (pricingMoveInDate) => {
    this.setState({ pricingMoveInDate });
  }

  setPricingTerm = (pricingTerm) => {
    this.setState({ pricingTerm });
  }


  render() {
    return (
      <div id="LeaseTerm" className="container px-5">
        <SelectionHeader title={SELECTION_HEADER.LEASETERMS} />
        <div className="row">
          <CustomDatePicker onChange={this.setPricingMoveInDate} value={this.state.pricingMoveInDate} mininmumDate={this.mininmumDate} />
          {!this.state.initialLoading
            ? [
              <CustomSelect key={'select'} label={SELECT_TYPE_LABEL.LEASETERM} options={this.state.pricingTermOptions} onChange={this.setPricingTerm} value={this.state.pricingTerm} includeAll={false} selectedValue={this.state.pricingTerm} />,
              <PricingCalendarButton key={'calendar'} viewPricingCalendar={this.viewPricingCalendar} />,
            ]
            : ''
          }
        </div>
        <div className='row'>
          {
            !this.state.loading && this.state.bestValueLoaded
              ? <PricingResults
                pricingResults={this.state.pricingResults}
                pricingMoveInDate={this.state.pricingMoveInDate}
                bestValueTerm={this.state.bestValueTerm}
                greatValueTerm={this.state.greatValueTerm}
                pricingTerm={this.state.pricingTerm}
                handleSelectLeaseTerm={this.handleSelectLeaseTerm} />
              : <Spinner />
          }
        </div>
        {!this.state.loading && this.state.bestValueLoaded ? <BackButton screen={Screens.APARTMENTS} /> : ''}
        {this.state.showPricingCalendarModal === true && (
          <PricingCalendarModal
            options={this.state.pricingTermOptions}
            selectedMoveInDate={this.state.pricingMoveInDate}
            selectedTerm={this.state.selectedTerm}
            closeModal={this.closePricingCalendar}
            saveModal={this.savePricingCalendar}
            bestValueTerm={this.state.bestValueTerm}
            minDate={this.mininmumDate}
          />
        )
        }
        {this.state.showConfirmSelectionModal && (<ConfirmSelectionModal closeModal={this.closeConfirmSelectionModal} openRegistrationModal={this.openRegistrationModal} />)}
        {this.state.showUserRegistrationModal && (<UserRegistrationModal closeModal={this.closeUserRegistrationModal} openModal={this.openConfirmSelectionModal} selectedRentableItems={[]} />)}
      </div>
    );
  }
}

Pricing.propTypes = {
  dispatch: PropTypes.func.isRequired,
  handleError: PropTypes.func.isRequired,
};

export default connect()(ErrorHandler(Pricing));
