import React from 'react';
import SelectionHeader from '../components/Common/SelectionHeader';
import { SELECTION_HEADER } from '../constants/Screens/Screens';
import { connect } from 'react-redux';
import { loadRentableItems } from '../services/UnitQuery/unitQueries';
import { setScreen } from '../redux/slices/configurationSlice';
import { Screens } from '../constants/Screens/Screens';
import { Constants } from '../constants/Common/Common';
import Spinner from '../components/Common/Spinner';
import BackButton from '../components/Common/BackButton';
import ConfirmSelectionModal from '../components/Modals/ConfirmSelectionModal';
import UserRegistrationModal from '../components/Modals/UserRegistrationModal';
import RentableItemResults from '../components/RentableItem/RentableItemResults';
import store from '../redux/store';
import { ErrorHandler } from '../services/utils/errorHandlingUtil';
import PropTypes from 'prop-types';

class RentableItems extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedRentableItems: [],
      displayUserRegistrationModal: false,
      displayConfirmSelectionModal: false,
      loading: true,
      rentableItemResults: [],
    };
  }

  getRentableItems = async () => {
    try {
      this.setState({ loading: true });
      const apartment = store.getState().userSelection.apartment;
      const data = await loadRentableItems(apartment.id);
      this.setState({ rentableItemResults: data });
    } catch (error) {
      this.props.handleError(error);
    } finally {
      this.setState({ loading: false });
    }
  };

  closeConfirmSelectionModal = () => {
    this.setState({ displayConfirmSelectionModal: false });
  };

  closeUserRegistrationModal = () => {
    this.setState({ displayUserRegistrationModal: false });
  };

  openRegistrationModal = () => {
    this.setState({ displayConfirmSelectionModal: false, displayUserRegistrationModal: true });
  };

  openConfirmSelectionModal = () => {
    this.setState({ displayConfirmSelectionModal: true, displayUserRegistrationModal: false });
  };

  setSelectedRentableItems = (selectedRentableItems) => {
    this.setState({ selectedRentableItems });
  };

  componentDidMount() {
    const floorPlan = store.getState().userSelection.floorPlan;
    if (Object.keys(floorPlan).length === 0) {
      this.props.dispatch(setScreen(Screens.FLOORPLANS));
    }

    const delayDebounceFn = setTimeout(() => {
      this.getRentableItems();
    }, 1000);
    return () => clearTimeout(delayDebounceFn);
  }

  render() {
    return (
      <div id="RentableItems" className="container px-5">
        <SelectionHeader title={SELECTION_HEADER.ITEMS} />
        <div className="floorplan-grid">
          {!this.state.loading
            ? <RentableItemResults rentableItemResults={this.state.rentableItemResults} selectedRentableItems={this.state.selectedRentableItems} setSelectedRentableItems={this.setSelectedRentableItems} />
            : <Spinner />}
        </div>
        <div style={{
          clear: 'both',
          paddingTop: '30px',
        }}>
          <div style={{
            display: 'inline-block',
            verticalAlign: 'middle',
          }}>
            {!this.state.loading
              ? <BackButton screen={Screens.LEASETERMS} />
              : ''
            }
          </div>
          <div style={{
            paddingLeft: '12px',
            display: 'inline-block',
            verticalAlign: 'middle',
          }}>
            {!this.state.loading
              ? <button className="target primary"
                button="primary"
                onClick={async () =>
                  this.setState({ displayConfirmSelectionModal: true })
                }
              >
                {Constants.APPLY_NOW}
              </button>
              : ''
            }
          </div>
          {
            this.state.displayConfirmSelectionModal && (
              <ConfirmSelectionModal closeModal={this.closeConfirmSelectionModal} selectedRentableItems={this.state.selectedRentableItems} openRegistrationModal={this.openRegistrationModal} />
            )
          }
          {
            this.state.displayUserRegistrationModal && (
              <UserRegistrationModal closeModal={this.closeUserRegistrationModal} openModal={this.openConfirmSelectionModal} selectedRentableItems={this.state.selectedRentableItems} />
            )
          }
        </div>

      </div>

    );
  }
}

RentableItems.propTypes = {
  dispatch: PropTypes.func.isRequired,
  handleError: PropTypes.func.isRequired,
};

export default connect()(ErrorHandler(RentableItems));
