import axios from 'axios';
import { store } from '../redux/store';

const getApiUrl = () => {
  let url =  store.getState().configuration.apiUrl;
  if(url.endsWith('/')) {
    url = url.slice(0, -1);
  }
  return url;
};

const coreApi = axios.create({
  baseURL: getApiUrl(),
  headers: {
    'Content-Type': 'application/json',
    Accept: 'application/json',
  },
});

