import { loadUnits } from '../../services/UnitQuery/unitQueries';

export const getApartments = async (body, setLoading, setApartmentResults, handleError) => {
  try {
    setLoading(true);
    const { records } = await loadUnits(body);
    setApartmentResults(records);
  } catch (error) {
    handleError(error);
  } finally {
    setLoading(false);
  }
};
